.portfolioPage {
  height: 100vh;
  width: 100vw;
  grid-gap: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
}

.project-card {
  width: 20vw;
  height: 350px;
  margin-left: 5px;
  color: black;
  padding: 16px;
  text-align: center;
  transition: 0.4s;
  text-decoration: none;
  border-radius: 5px;
  border: 1px solid black;
  background-color: #f1f1f1;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.project-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.portfolioPage .flex {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

@media only screen and (max-width: 800px) {
  .flex {
    display: flex;
    flex-direction: column;
    gap: 20px;
    height: 800px;
  }

  .project-card {
    width: 30vw;
  }
}
